<template>
  <div class="about">
    <h1 class="title">Bookmarks</h1>
    <countryCardBookmark v-if="showDetails" :img="img" :name="name" :independent="independent" :lang="lang" :currency="currency" :continent="continent" :region="region" :subregion="subregion" :population="population" :timezone="timezone"/>
    <h4 v-if="clickCountryPrompt && load==false" class="prompt">CLICK A COUNTRY</h4>
    <h1 v-if="load" aria-busy="true" class="load"></h1>
    <div class="grid1">
      <countryCardBookmarkIndex v-for="(country) in bookmarks" :img="country.countryImg" :name="country.countryName" :key="country._id" @click="loadDetails(country.countryImg, country.countryName, country.independent, country.countryLanguage,country.countryCurrency,country.countryContinent, country.countryRegion, country.countrySubregion, country.countryPopulation, country.countryTimezone)" @delete-me="deleteBookmark(country._id)"/>
    </div>
  </div>
</template>

<script>
import countrymon from "../countrymon"
import countryCardBookmark from '@/components/countryCardBookmark.vue'
import countryCardBookmarkIndex from '@/components/countryCardBookmarkIndex.vue'
export default {
  name: 'BookmarkView',
  components:{countryCardBookmark,countryCardBookmarkIndex},
  data(){
    return{
      bookmarks:[],
      load:true,
      showDetails:false,
      clickCountryPrompt:true,
      img:"",
      name:"",
      independent:false,
      lang:"",
      currency:"",
      continent:"",
      region:"",
      subregion:"",
      population:0,
      timezone:""
    }
  },
  async created(){
    this.bookmarks = await countrymon.getBookmarks()
    this.load = false
  },
  methods:{
    loadDetails(img,name,indepence,lang,currency,continent,region,subregion,population,timezone){
        this.img = img
        this.name = name
        this.independent = indepence
        this.lang = lang
        this.currency = currency
        this.continent = continent
        this.region = region
        this.subregion = subregion
        this.population = population
        this.timezone = timezone
        this.showDetails = true
        this.clickCountryPrompt = false
    },
    async deleteBookmark(id){
      await countrymon.deleteBookmarks(id)
      .then(()=>alert("Bookmark Deleted"))
      .catch((err)=>alert(err))
      window.location.reload()
    }
  }
}
</script>

<style scoped>
.load{
  z-index: 2;
  left: 50%;
  display: fixed;
  margin-top: 150px;
}
.prompt{
    height: 100%;
    left: 65%;
    top: 50%;
    z-index: 1;
    width: 500px;
    position: fixed;
    color: grey;
}

.title{
  left: 44%;
  top: 10%;
  position: fixed;
}
</style>